import React from "react";

function Note({id, title, content, deleteItem}) {
  const deleteNote = () =>{
    deleteItem(id)
    // console.log('Clicked Note')
  }
  return (
    <>
    <div className="note flex flex-col shadow-xl	border m-10 p-2 pt-4 w-72 rounded-xl">
       <h1 className="text-xl font-medium	mb-4 outline-none">{title}</h1>
       <p>{content}</p>
      <div className="flex justify-end content-center ">
      <div onClick={deleteNote} className="del rounded-full border p-2 hover:bg-yellow-400">
      <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 " fill="none" viewBox="0 0 24 24" stroke="currentColor">
  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
</svg></div>


      </div>

    </div>
    </>
  );
}

export default Note;




