import React, { useState } from 'react'
import '../index.css';

function CreateNote({passNote}) {
  const [expand, setExpand] = useState(false)
  const [note, setNote] = useState({
    title:"",
    content:"",
  });
  const getInput = (e) =>{
    const {value, name} = e.currentTarget
    // const val = e.currentTarget.value
  
    setNote((prevVal) =>{
      return {
        ...prevVal,
        [name]: value,
      }
    })
    // console.log(note);

  }
  const addEvent = (e) =>{
    e.preventDefault();
    passNote(note)
    setNote({
      title:"",
      content:"",
    })
  }

  const expandElem = () =>{
    setExpand(true)
  }

  const doubleClicked = () =>{
    expand? setExpand(false) : setExpand(true)

  }
  return (
    <>
      <div className="mainNote" onDoubleClick={doubleClicked}>
        <form className="shadow-xl	border m-10 p-2 pt-4 w-96 rounded-xl" action="" >
        {expand?
          <input type="text" className="text-xl font-medium	mb-4 outline-none" placeholder="Title" onChange={getInput} name="title" value={note.title}/> :null}
          <textarea className="outline-none"  id="" cols="" rows="" placeholder='Write a note..' onClick={expandElem} onChange={getInput} name="content" value={note.content}></textarea>
          <div className='flex items-center justify-end	'>
          <button className="rounded-full border p-2 hover:bg-yellow-400" onClick={addEvent}><svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
</svg></button>
            
          </div>
        </form>
      </div>
    </>
  );
}

export default CreateNote;
